<template>
	<view class="warp">
		<view class="inpt-content">
			<textarea maxlength="300"  placeholder="闲置简介" v-model="content" class="inpt-text"></textarea>
		</view>
		<view class="inpt-content">
			<textarea maxlength="100" placeholder="输入联系方式" v-model="contact" class="inpt-text"></textarea>
		</view>
		 
		<view class="inpt-imge">
			<uni-file-picker 
				:auto-upload="false"
				class="upload"
				v-model="imageValue"
				fileMediatype="image" 
				mode="grid" 
				:limit="6"
				@select="select" 
				@progress="progress" 
				@success="success" 
				@fail="fail" 
				@delete="remove"
				
			>
			<image style="width: 30px;height: 30px;" src="../../static/images/icon/pic.png"></image>
			</uni-file-picker>
		</view>
		
		<view @click="sead()" class="inpt-sead">
			<span v-if="loading==0">发送</span>
			<span v-else-if="loading==1">发送中</span>
			<span v-else>图片上传中</span>
			</view>
	</view>
</template>

<script>
	import {commit} from "@/api/addSell.js"
	export default {
		data() {
			return {
				sellId:null,
				content:"",
				contact:"",
				loading:0,
				imageValue:[]
			}
		},
		methods: {
			// 获取上传状态
			async select(e){
				console.log(e)
				//和提交一起上传
				e.tempFiles.forEach(file=>{
				
					const param = {
						'width' : file.image.width,
						'height': file.image.height,
						"name":'sell/' + file.name,
						"extname":file.extname,
						"url":file.url
					}
					this.imageValue.push(param);
				});		
			},
			// 获取上传进度
			progress(e){
				// console.log('上传进度：',e)
				
			},
			
			// 上传成功
			success(e){
				// console.log('上传成功')
			},
			
			// 上传失败
			fail(e){
				// console.log('上传失败：',e)
			},
			//移除文件
			remove(e){
				// console.log(this.imageValue)
			},
			sead(){
				if(this.loading!=0)
					return;
				if(this.stringUtils.isEmpty(this.content)){
					uni.showToast({
						title: "内容不能为空！",
						icon:"none",
						duration:850
					});
					return;
				}
				if(this.stringUtils.isEmpty(this.contact)){
					uni.showToast({
						title: "联系方式不能为空！",
						icon:"none",
						duration:850
					});
					return;
				}
				this.loading=1;
				//异步执行
				this.$store.dispatch("addSell",{
					images:[...this.imageValue],
					params:{
						contact:this.contact,
						content:this.content,
						sellPictures:[]
					}
				})
				wx.showModal({
				  title: '提示',
				  content: '发送成功，已提交审核，审核时间1-2分钟',
				  success (res) {
				   uni.navigateBack();
				  }
				})			
			}
		}
	}
</script>

<style>
.inpt-text{
	width: calc( 100% - 20px );
	margin-top: 10px;
	padding: 10px;
	height: 250rpx;
	box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px, rgb(51, 51, 51) 0px 0px 0px 3px;
	border-radius: 10px;
}
.inpt-sead{
	width: 100%;
	height: 100rpx;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #fccd43;
	border-radius: 10px;
	
}
.inpt-imge{
	margin: 100rpx 0;
	
}
.uni-file-picker{


}
</style>
